/*Style Board Game*/
#board{
    flex-basis: 140%;
    padding: 15px;
}

#view{
    position: initial;
    /* height: 90vh; */
    align-items: center;
    margin-bottom: 5em;
}

.boardContent{
    border-radius: 12px;
    background-color : rgba(26, 188, 156, 0.80) ;
}

/*Inicio*/
#contentInicio{
    padding: 2em;
}

/*Style visor*/

.visor{
    width: min-content;
    border-radius: 25px;
    padding: 5px;
    height: min-content;
}

.visor>.tag{
    padding:4px;
    font-size: 12px;
    font-weight: bold;
}

.visor>.value{
    padding: 7px 4px 4px 4px;
    border-radius: 0px 25px 25px 0px;
    width: 45px;
}

.rs-btn-default.DestravarDica{
    width: min-content;
    padding: 7px 12px !important;
}

/* Modal */
.modal-personalizado{
    display: none;
    z-index: 1000;
    width: 100%;
    overflow: auto;
    position: fixed;
    top: 0;
    left: 0px;
    min-height: 100%;
    height: 100%;
    background-image: url(../assets/69.jpg);  
    /* Center and scale the image nicely */
    background-size: cover;
    background-color: white;
}

.modal-body{
    display: flex;
    min-height: 100vh;
}

.modal-fechar{
    position: absolute;
    bottom: 0;
    right: 0;
}


.modal-fechar> *:hover, .modal-fechar> *> *:hover{
    color: white !important;
}

.modal-body > .display{
    margin-top:2em;
}

    

/* Instrucoes */
.dialogo, .dialogoContent, .dialogoContentInfo{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}


.containerdialogoFixo{
    height:7em;
}


.dialogoFixo{
    display: inline;
    position: relative;
    top: 2.5em;
    border-radius: 12pt;
}


.dialogoFixo > a > .logo{
    background-color: white;
}

.dialogoFixo > a >.logo > img{
    width: 20em;
}

.dialogoTitle{
    display: inline;
    padding: 0.5em 1.5em;
    position: relative;
    top: 2.5em;
    left: 0;
    z-index: 1;
    border-radius: 6px;
}

.dialogoContent{   
    align-items: center;
    display: flex;
    padding: 1em 1.5em;
    min-height: 65vh;
    height: 65vh;
    border-radius: 12px;
}

.dialogoContentInfo{
    align-items: center;
    display: flex;
    padding: 1em 1.5em;
    min-height: 85vh;
    height: 85vh;
    border-radius: 12px;
}

.dialogoContent.vitoria{
    padding: 0.2em 1.2em;
}

.dialogoContent>h2{
    animation-name: hide_show;
    animation-timing-function: ease-in-out;
    animation-duration: 1s;
}

.dialogoNav > *{
    margin: 1em;
}

.logoDivulgacao > a >.logoPatrocinador > img {
    width:90%;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
}


.logoDivulgacao > a  {
    display: flex;
    width:15%;
    min-width: 90px;
    max-width: 150px;
}

.logoDivulgacao > a >.logoPatrocinador{
    display: flex;
    background-color: white;
    padding: 0.5em;
    margin-top: 0.2em;
    flex-direction: column;
    justify-content: center;
    border-radius: 12pt;
    width:100%;
    min-height: 46px;
    align-items: center;
}

.logoDivulgacao{
    flex-wrap: wrap;
    padding-top: 1.5em;
    position: relative;
    display: flex;
}

.dialogoMain{
    margin-top: 3em;
    height: 80%;
}

.dialogoMainInfo{
    height: 80%;
}

.dialogoMain.modo{
    margin-top: 3%;
}

.dialogoMainVitoria{
    height: 94%;
    border-radius: 12px;
}

.dialogoRodape{
    height: 10%;
    font-size: 2em !important;
    align-items: flex-end;
}

.fakemodo{
    height: 78px;
    margin: 0 !important;
}


.iconShare, .iconPg, a.iconShare{
    color: white;
    transition: all 1s cubic-bezier(0, 0, 0.01, 0.98) 0.1s;
}

.iconShare:hover, .iconPg:hover{
    color: #1abc9cff ;
}

.navicon{
    color: #4d4d4dff; ;
    transition: all 1s cubic-bezier(0, 0, 0.01, 0.98) 0.1s;
}

.navicon:hover{
    color: #1abc9cff ;
}



.buttonIncial.rs-btn-default{
    margin-top: 2rem;
}


.buttonsTips > .button1{
    width: 56px;
    text-align: center;
    margin: 0.4em;
    font-size: 22px !important;
    font-weight: bold;
    padding: 4px 16px 4px 16px !important;
}

.buttonsTips{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: row;
}

.disposicao-dicas{
    flex-wrap: wrap;
}

.button-board{
    width: 100%;
    height: 4vh;
}

.button-board > button{
    padding: 0.1em;
    width: 100%;
    height: 4vh;
    outline-color: transparent;
}

.main-board{
    width: 100%;
}

.card{
    text-align: justify;
    padding: 15px;
}

.rs-modal-footer{
    padding: 20px;
    text-align: center !important;
}

.ajuda{
    border-radius: 12px;
    padding: 10px;
    margin-top: 10px;
    width: fit-content;
    text-align: center;
}

.palavraCerta{
    text-align: center;
    margin: 9px 9px 24px 9px;
    padding: 20px;
    border-radius: 12px;
    width: auto;
    text-align: center;
    color: rgb(10, 65, 54);
    background: rgb(255, 210, 134);
    border-color: rgb(10, 65, 54);
    border-width: 2px;
    border: solid;
    box-shadow: rgb(252, 211, 143) 0px 0px 0px 10px;
}

.palavraCertaVitoria{
    display: inline-block;
    text-align: center;
    margin: 9px 20px 24px 20px;
    padding: 10px 0px;
    border-radius: 12px;
    width: 190px;
    text-align: center;
    color: rgb(10, 65, 54);
    background: rgb(255, 210, 134);
    border-color: rgb(10, 65, 54);
    border-width: 2px;
    border: solid;
    box-shadow: rgb(252, 211, 143) 0px 0px 0px 10px;
}

.modal-dicas{
    display: none;
    overflow-x: hidden;
}

#dicas{
    height: 0px;
}

#buttonDica{
    position: relative;
    top: -80px;
}

/*Style Buttons*/


.buttonR, .buttonR.rs-btn-default{
    border-radius: 50% !important;
    width: 50px !important;
    height: 50px !important;
    padding: 0 !important;
    text-align: center !important;
}



.button1.rs-btn-default{
    border-style: solid ;
    border-width: 0.5px ;
}


.button1.rs-btn-default:active, .button1.rs-btn-default:focus, .button1.rs-btn-default:hover{
    top: 7px;
    box-shadow: none;
}

.rs-btn-default{
    padding: 0.5rem 2rem !important;
}


.rs-btn-group.rs-btn-group-vertical >.buttonGroup{
    border-radius: 6px;
    margin-bottom: 2rem;
   
}

.buttonGroup{
    width: 160px ;
    text-align: center;
    padding: 9px 0px 7px 0px !important;
}

/*Personalização modal Game Finish*/

.rs-modal-dialog, .rs-modal, div.rs-modal-body{
    margin: 0px !important;
    width: 100% !important;
    height: 100% !important; 
}

.rs-modal-content{
    height: 100% !important;
}

.confetti{
    position: absolute !important;
    z-index: 2000;
    top: 70%;
    right: 100%;
}


/* Tipologia */
h1{
    font-size: 42px !important;
    font-weight: bold !important;
}

h2{
    font-size: 28px !important;
    font-weight: bold !important;
}

h3{
    font-size: 22px !important;
    font-weight: 500 !important;
}

h4{
    font-size: 18px !important;
    font-weight: 600 !important;

}

h5{
    font-size: 16px !important;
    font-weight: 500 !important;
}

h6{
    font-size: 14px !important;
    font-weight: bold !important;
}

p{
    font-size: 12px !important;
    font-weight: 400 !important;
}

@media (min-width: 450px) {
    .disposicao-dicas{
        flex-wrap: nowrap;
    }
}




@media (min-width: 769px) , (min-height:769px) and (orientation: landscape) {
   /*Style Board Game*/
    #view{
        margin-bottom: 0px;
        /* padding-top: 8vh;
        height: fit-content; */
    }

    #board{
        padding: 15px 0px 15px 15px;
    }


    .visor>.tag{
        padding:6px;
        font-size: 14px;
        font-weight: bold;
    }

    #dicas{
        margin-top: 0px;
        height: initial;
    }


    

    /* Inicio */
    .containerdialogoFixo{
        height:5em;
    }

    .dialogoFixo{
        display: inline;
        position: relative;
        top: 0.5em;
        border-radius: 12pt;
    }
    
    
    .dialogoFixo > .logo{
        background-color: white;
    }
    
    
    /* Instrucoes */
    

    .dialogoTitle{
        padding: 2em 3em;
        top: 3.5em;
    }

    .dialogoContent, .dialogoContentInfo{   
        padding: 3.5em 4em;
    }

    .dialogoContent.vitoria{
        padding: 2.5em 3em;
    }

    .dialogoContentInfo{
        min-height: 65vh;
        height: 65vh;
    }


    /*Style tips*/
    .rs-modal-body{
        overflow: hidden;
    }

    .rs-modal-dialog, .rs-modal{
        margin: auto !important;
        width: min-content !important;
        height: auto !important; 
    }

    .rs-modal-dialog{
        margin-top: 20% !important;
    }

    .button-board{
        height: 5.5vh;
    }

    .button-board > button{
        height: 5.5vh;
    }

    .scrollAuto{
        overflow: auto;
    }
    .modal-dicas-sm{
        display: none;
    }

    /* #buttonDica{
    position: relative;
    top: -80px;
    } */

    .modal-dicas{
        display: block;
    }

    /*Style tips*/
    .display{
        max-height: 100%;
        overflow: auto;
        margin-top: 1em;
    }

    /* Tipologia */
    h1{
        font-size: 48px !important;
        font-weight: bold !important;
    }

    h2{
        font-size: 38px;
        font-weight: bold !important;
    }

    h3{
        font-size: 28px !important;
        font-weight: 500 !important;
    }

    h4{
        font-size: 20px !important;
        font-weight: 600 !important;

    }

    h5{
        font-size: 18px !important;
        font-weight: 500 !important;
    }

    h6{
        font-size: 16px !important;
        font-weight: bold !important;
    }

    p{
        font-size: 14px !important;
        font-weight: 400 !important;
    }
}

@media (min-width: 1200px) {
    /* Instrucoes */
    .dialogoTitle{
        top: 3.5em;
        animation-name: deslocamento;
        animation-timing-function: ease-in-out;
        animation-duration: 1s;
    }

    .dialogoMain{
        margin-top: 4%;
        height: 84%;
    }

    .dialogoContent , .dialogoContentInfo{
        padding: 4.5em 6em;
    }

    .dialogoContent.vitoria{
        padding: 3.5em 5em;
    }


    
    /*Style Board*/

    .button-board{
        height: 5vh;
    }

    .button-board > button{
        height: 5vh;
    }

    /*Animações*/

    @keyframes deslocamento {
        0%   {top: 0;}
        100% {top: 3.5em;}
    }

    @keyframes hide_show {
        from{color: transparent; }
        to{color: #4d4d4dff;}
    }

    /* Styles tips*/
    .card{
        flex: 50%;
    }

      /* Tipologia */
    h1{
        font-size: 56px !important;
        font-weight: bold !important;
    }

    h2{
        font-size: 40px !important;
        font-weight: bold !important;
    }

    h3{
        font-size: 32px !important;
        font-weight: 500 !important;
    }

    h4{
        font-size: 22px !important;
        font-weight: 600 !important;

    }

    h5{
        font-size: 18px !important;
        font-weight: 500 !important;
    }

    h6{
        font-size: 16px !important;
        font-weight: bold !important;
    }

    p{
        font-size: 14px !important;
        font-weight: 400 !important;
    }
}