/*Tipografia*/
*{
    font-family: 'Fira Sans', sans-serif;
}




Title, h1.subTitle{
    font-size : 3.5em !important;
    /* height : 1.2em; */
}

.subTitle, .subTitle.rs-btn-default, h1.subTitle{
 font-size: 2em !important;
}

.magro{
    font-weight: inherit;
    line-height: 1.5;
}

.sizeButton{
    font-size : x-large !important;
}

.destaque1{
    font-weight: bold;
}

/*Theme-Color*/

.Color1 {
    color : #1abc9cff;
}

.Color2 , .iconShare.Color2{
    color : #4D4D4D;
}

.Color3, .Color3.rs-btn-default {
    color : #fff;
}

.Color4, .Color4.rs-btn-default, .iconShare.Color4{
    color: black !important;
}


.bkColor1 {
    background-color : #1abc9cff !important;
}

.bkColor1.opacidade {
    background-color : rgba(26, 188, 156, 0.99) !important;
}

.bkColor2 {
    background-color : #4d4d4dff;
}

.bkColor3, .card {
    background-color : #fff;
}

.bkColor3.opacidade {
    background-color :rgba(255, 255, 255, 0.88) !important;
}

.bkColor4{
    background-color: black;
}

.bkColor5{
    background-color: #737373;
}

.bkColor6{
    background-color: rgba(26, 188, 156, 0.30);
}




.borColor1, .borColor1.rs-btn-default {
    box-shadow: 0 7px 0 #0a4136;
    border-color:  #4EDEC1; 
    background-color:  #4EDEC1; 
}

.borColor2,.borColor2.rs-btn-default {
    box-shadow: 0 7px 0 hsl(0, 0%, 13%);
    border-color:  #4d4d4dff;
    background-color:  #4d4d4dff;
}

.borColor3,.borColor3.rs-btn-default {
    box-shadow: 0 7px 0 hsl(0, 0%, 13%);
    border-color:  #fff;
    background-color:  #fff
}

/*Color Tratamento*/
.borColor1.rs-btn-default:hover,.borColor1.rs-btn-default:active, .borColor1.rs-btn-default:focus, .borColor1.rs-btn-default:active:hover{
    background-color:  #1abc9cff; 
}
.borColor2.rs-btn-default:hover,.borColor2.rs-btn-default:active, .borColor2.rs-btn-default:focus, .borColor2.rs-btn-default:active:hover{
    background-color:  #4d4d4dff; 
}

.Color3.rs-btn-default:active:hover, .Color3.rs-btn-default:active, .Color3.rs-btn-default:hover, .Color3.rs-btn-default:focus{
    color: white;
}
.Color3 > a:hover{
    color: #fff;    
}

.buttonR.rs-btn-default.rs-btn-disabled{
    box-shadow: 0 7px 0 rgb(54, 54, 54); 
    border-color:  #c5c6c7;
    background-color: #c5c6c7;
    color: #4D4D4D; 
}

/*Geral*/

body{
    background-image: url(../assets/69.jpg);  
    /* Center and scale the image nicely */
    background-size: cover;
}


.dialogoMainVitoria{
    background-image: url(../assets/bgVitoria.jpg);
    background-size: contain;
    background-position: bottom;
}


#root{
    background-color: rgba(255, 255, 255, 0.88);
    min-height: 100vh;
}

/*Removendo os undelines relacionados ao link*/
a:hover, a:focus{
    text-decoration: none!important;
}

.iconShare.Color2:hover{
    color : #1abc9cff;
}


@media (min-width: 769px) , (min-height:769px) and (orientation: landscape) {
    /*Tipografia*/
    .Title, h1.Title{
        font-size : 4.5em !important;
    }

    .subTitle, .subTitle.rs-btn-default, h1.subTitle{
    font-size: 3em !important;
    }
}
@media (min-width: 1200px) {

    /*Geral*/
    #logo{
        display: block !important;
        width: 10vh;
    }

    /*Tipografia*/
    .Title, h1.Title{
        font-size : 5.5em !important;
    }

    .subTitle, .subTitle.rs-btn-default, h1.subTitle{
        font-size: 4em !important;
    }
}    