/*Ajuste Frae,*/

/*Layout*/

.ajusteHeight{
    height: fit-content;
}

.container{
    position: relative;
    top: -2em;
    display: flex;
    padding: 3% 3% 0 3%;
}

.containerModal.modal-body{
    padding: 3% 3% 0 3% !important;
}

.col, .col-sm, .main-board, .col-lg, .col-md{
    display: flex;
    flex-direction: column;
}

.l6, .m6{
    width: 100%;
}

.heigthOverInfo{
    height: 80vh;
}

.heigthOverInfo > div{
    overflow-y: auto;
}


.sesson > h5{
    margin-top: 1.2rem;
    margin-bottom: 0.2rem;
}

.sm-col, .row, .col-board, .row-sm{
    display: flex;
    flex-direction: row;
}

/* Nav Main-side */

.main-side{
    display: flex;
    flex-direction: column;
}

#nav{
    box-shadow: 0px 1px 6px 0px #00000073;
    height: 56px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#subnav{
    display: flex;
    flex-direction: row;
}

#subnav > *{
    margin-left: 12px;
    margin-right: 12px;
}

#subnav > .navicon{
    height: 42px;
    padding-top: 7px;
}

.logonav{
    width: 42px;
}

.text-center{
    text-align: center;
}

.text-creditos{
    text-align: center;
}


.justify{
    justify-content: start;
    text-align: start;
}


.center{
    justify-content: center;
}

.end{
    justify-content: flex-end;
}


.distributed5{
    justify-content: space-around;
}

.distributed{
    justify-content: space-around;
}

.distributed2{
    justify-content: flex-end;
}

.distributed3{
    justify-content: space-between;
}
.distributed4{
    justify-content: space-evenly;
}


.cente-vertical{
    align-items: center;
}

.ajuste-vertical{
    align-items: stretch;
}

.baselinelayout, .baselinelayoutfull{
    min-height: 100vh;
    height: auto;
    align-items: center;
}


.border1{
    border-radius: 12px;
}
.margin-min{
    margin: 15px;
}

.margin-top{
    margin-top: 2em;
}


.padding-creditos{
    padding-top: 9em;
}

.padding-min{
    padding: 2em;
}

.padding-small{
    padding: 10px;
}

.padding-top-bottom{
    padding: 1.5em 0em;
}

.padding-top{
    padding-top: 1.5em;
}

.fullWidth{
    width: 100%;
}

.fullHeight{
    height: 100%;
}

.medHeight{
    height: 60%;
}


.fullWidthContent > *{
    width:100%;
}

/*Responsividade*/

.hide-sm {
    display: none  !important;
}

.sesson{
    text-align: center;
}
.divider{
    display: none;
}

@media (min-width: 769px) , (min-height:769px) and (orientation: landscape) {
/*Layout*/
.distributed2{
    justify-content: center;
}

.container{
    position: initial;

    width: 100%;
    padding: 0 5% 0 5%;
    justify-content: center;
}

.containerModal.modal-body{
    padding: 0 5% 0 5% !important;
}

.baselinelayout{
    height: 90vh;
    align-items: center;
}

.baselinelayoutfull{
    height: 100vh;
    align-items: center;
}

.text-creditos{
    text-align: start;
}

/*Responsividade*/


.hide-sm {
    display: flex !important;
}

.sm-col{
    display: flex;
    flex-direction: column;
}

.col-sm{
    display: flex;
    flex-direction: row;
}

.col-md{
    display: flex;
    flex-direction: row;
}

.col-lg{
    display: flex;
    flex-direction: column;
}


 .col-md>.m6:first-of-type, .main-board>.m6:first-of-type{
    /* padding-right: 4em; */
}

 .col-md>.m6:last-of-type, .main-board>.m6:last-of-type{
    padding-left: 4em;
}


.padding-creditos{
    padding-top: 0em;
}

.m6{
    width: 50%;
    padding: 1em;
}

.divider{
    display: block;
    width: 1px;
    height: 40%;
    position: relative;
    right: 50%;
    top: 30%;
    background: black;
}


.first{
    order: 0 ;
}

.second{
    order: 1 ;
}

.third{
    order: 2 ;
}

.row-sm.distributed{
    display: flex;
    flex-direction: column;
    justify-content: start;
}


/* Nav Main-side */

.main-side{
    display: flex;
    flex-direction: row;
}

#nav{
    height: 100vh;
    width: 80px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#subnav{
    display: flex;
    align-items: center; 
    flex-direction: column;
}

#subnav > *{
    margin-top: 12px;
    margin-bottom: 12px;
}



.distributed5{
    justify-content: space-between;
}


}




@media (min-width: 1200px) {

    /* Responsividade */
    
    .col-lg{
        display: flex;
        flex-direction: row;
    }
    

    .col>.l6:first-of-type, .col-lg>.l6:first-of-type, .main-board>.l6:first-of-type{
        padding-right: 4em;
    }
    
    .col>.l6:last-of-type, .col-lg>.l6:last-of-type, .main-board>.l6:last-of-type{
        padding-left: 4em;
    }

    .sesson{
        text-align: start;
    }

    .sesson > h4{
        margin-top: 0;
        margin-bottom: 0.2rem;
    }

    .l6{
        width: 50%;
        padding: 1em;
    }

    /*Layout*/
    .container{
        position: initial;
        width: 100%;
        padding: 0 12% 0 12%;
        justify-content: center;
    }

    .containerModal.modal-body{
        padding: 0 12% 0 12% !important;
    }

    

    .distributed5{
        justify-content: space-between;
    }

    .heigthOverInfo > div{
        overflow-y: initial;
    }
    
}


